:root {
  --opehuone--container--width: 1410px;
  --opehuone--container--padding: 1rem;
  --opehuone--spacing--2-x-small: 0.875rem;
  --opehuone--spacing--x-small: 1rem;
  --opehuone--spacing--small: clamp(1.5rem, 2vw, 1.875rem);
  --opehuone--spacing--normal: clamp(1.8rem, 3vw, 2.5rem);
  --opehuone--spacing--medium: clamp(
  	2.8rem,
  	4vw,
  	3.125rem
  );
  --opehuone--spacing--large: clamp(3rem, 5vw, 3.75rem);
  --opehuone--spacing--2-x-large: clamp(
  	5rem,
  	7vw,
  	7.5rem
  );
  --opehuone--font-size--x-small: 0.75rem;
  --opehuone--font-size--small: 0.875rem;
  --opehuone--font-size--normal: 1rem;
  --opehuone--font-size--medium: clamp(1rem, 2vw, 1.125rem);
  --opehuone--font-size--2-x-medium: clamp(1.125rem, 2vw, 1.25rem);
  --opehuone--font-size--large: clamp(
  	1.125rem,
  	2vw,
  	1.5rem
  );
  --opehuone--font-size--x-large: clamp(1.5rem, 3vw, 2rem);
  --opehuone--font-size--2-x-large: clamp(
  	2rem,
  	3.5vw,
  	2.5rem
  );
  --opehuone--font-size--3-x-large: clamp(
  	2.5rem,
  	4vw,
  	3rem
  );
}

button {
  cursor: pointer;
}

.opehuone-content-container {
  max-width: var(--opehuone--container--width);
  width: 100%;
  padding-left: var(--opehuone--container--padding);
  padding-right: var(--opehuone--container--padding);
  margin-left: auto;
  margin-right: auto;
}

.hds-container {
  max-width: var(--opehuone--container--width);
  width: 100%;
  padding-left: var(--opehuone--container--padding);
  padding-right: var(--opehuone--container--padding);
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .hds-container {
    padding-left: var(--opehuone--container--padding);
    padding-right: var(--opehuone--container--padding);
  }
}
@media (min-width: 1248px) {
  .hds-container {
    max-width: var(--opehuone--container--width);
    padding-left: var(--opehuone--container--padding);
    padding-right: var(--opehuone--container--padding);
  }
}

.hds-container--wide {
  max-width: 1920px;
}

.date-box-row {
  display: grid;
  align-items: center;
  grid-template-columns: auto 90px;
  gap: 0.75rem;
}

.date-box-row-weather {
  font-size: var(--opehuone--font-size--medium);
  font-weight: 700;
  text-align: center;
}

.date-box-week {
  display: block;
  font-size: var(--opehuone--font-size--medium);
}

.date-box-month {
  display: block;
  font-size: var(--opehuone--font-size--medium);
  font-weight: 700;
}

.date-box-info {
  margin-top: var(--opehuone--spacing--small);
  display: block;
  font-weight: 500;
  font-size: var(--opehuone--font-size--small);
}

.opehuone-form-field-group {
  margin-bottom: 1rem;
}

.opehuone-form-label {
  font-weight: bold;
  font-size: 0.875rem;
  line-height: 1.1428571429;
  margin-bottom: 0.5rem;
}

.opehuone-form-field-description {
  font-size: 0.875rem;
}

.opehuone-form-label-sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.opehuone-form-mandatory-indication {
  margin-left: 0.5rem;
  font-size: 1.125rem;
  font-weight: 700;
  color: #000;
}

.opehuone-form-control {
  display: block;
  width: 100%;
  height: auto;
  padding: 1rem 1.25rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.1875;
  color: #000;
  background-color: #fff;
  background-clip: padding-box;
  border: 0.0625rem solid #000;
  border-radius: 0;
  box-sizing: border-box;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
}
.opehuone-form-control:focus {
  outline: 0;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.75);
}
.opehuone-form-control::placeholder {
  color: #000;
  opacity: 1;
}

/**
 * Gravity forms
 * Comment out if needed
 */
.gsection_title {
  color: var(--wp--preset--color--accent-2);
}

.side-links-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 0.625rem;
  grid-template-columns: auto;
}
.side-links-list__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap;
  gap: 0.625rem;
}
.side-links-list__link {
  color: #000;
  font-size: var(--opehuone--font-size--normal);
  font-weight: 700;
  text-decoration: none;
}
.side-links-list__link:hover, .side-links-list__link:focus {
  color: #000;
  text-decoration: underline;
}
.side-links-list__remove-btn {
  display: none;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}
.side-links-list__remove-btn > svg {
  width: 1.25rem;
  height: auto;
}
.side-links-list-box--modification-ongoing .side-links-list__remove-btn {
  display: inline-block;
}
.side-links-list__edit-link {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  gap: 0.625rem;
  background: none;
  border: none;
  font-size: var(--opehuone--font-size--medium);
  font-weight: 700;
  text-decoration: none;
  padding: 0;
}
.side-links-list__edit-link:hover, .side-links-list__edit-link:focus {
  text-decoration: underline;
}
.side-links-list__own-links-functions {
  margin-top: var(--opehuone--spacing--normal);
}
.side-links-list__form-wrapper {
  display: none;
}
.side-links-list-box--modification-ongoing .side-links-list__form-wrapper {
  display: block;
  margin-top: var(--opehuone--spacing--normal);
}
.side-links-list__reset-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-weight: 500;
  color: #fff;
  background-color: red;
  text-align: center;
  text-decoration: none;
  white-space: normal;
  vertical-align: middle;
  user-select: none;
  border: 2px solid red;
  padding: 0.625rem 2rem;
  font-size: var(--opehuone--font-size--normal);
  border-radius: 0;
  line-height: 1.5;
  min-height: 56px;
  width: 100%;
  display: flex;
}
.side-links-list__reset-btn:hover, .side-links-list__reset-btn:focus {
  color: red;
  background-color: transparent;
  text-decoration: none;
}
.side-links-list__reset-btn:focus {
  outline: 0;
  display: inline-flex;
}
.side-links-list__reset-btn.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}
.side-links-list__reset-btn.side-links-list__reset-btn--final--hidden {
  display: none;
}
.side-links-list__reset-buttons {
  display: grid;
  grid-template-columns: auto;
  gap: var(--opehuone--spacing--x-small);
}
.side-links-list__form {
  margin-bottom: var(--opehuone--spacing--normal);
}

.own-links__add-new-form__legend {
  font-size: 1rem;
  font-weight: 600;
}
.own-links__add-new-form__form-group {
  margin-bottom: 1rem;
}
.own-links__add-new-form-notifications {
  margin-top: 1.25rem;
  padding: 0.625rem;
  font-size: 0.9375rem;
  display: none;
  color: #000;
  border: 1px solid #000;
}
.own-links__submit-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-weight: 500;
  color: #fff;
  background-color: #000;
  text-align: center;
  text-decoration: none;
  white-space: normal;
  vertical-align: middle;
  user-select: none;
  border: 2px solid #000;
  padding: 0.625rem 2rem;
  font-size: var(--opehuone--font-size--normal);
  border-radius: 0;
  line-height: 1.5;
  min-height: 56px;
  width: 100%;
  display: flex;
}
.own-links__submit-btn:hover, .own-links__submit-btn:focus {
  color: #000;
  background-color: transparent;
  text-decoration: none;
}
.own-links__submit-btn:focus {
  outline: 0;
  display: inline-flex;
}
.own-links__submit-btn.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}

.opehuone-grid {
  display: grid;
  grid-template-columns: auto;
  gap: var(--opehuone--spacing--normal);
  width: 100%;
}
@media (min-width: 768px) {
  .opehuone-grid.opehuone-grid--reversed {
    grid-template-columns: 360px 1fr;
  }
}
@media (min-width: 768px) {
  .opehuone-grid {
    grid-template-columns: 1fr 360px;
  }
}

:where(.wp-site-blocks) > footer.wp-block-template-part {
  margin-block-start: 0;
}

.sidebar-boxes {
  display: flex;
  flex-direction: column;
  gap: var(--opehuone--spacing--normal);
}

.sidebar-box {
  padding: 1.6875rem 1.25rem;
}
.sidebar-box.sidebar-box--suomenlinna-light {
  background-color: var(--color-suomenlinna-light);
}
.sidebar-box.sidebar-box--coat-of-arms-light {
  background-color: var(--color-coat-of-arms-light);
}
.sidebar-box.sidebar-box--engel-light {
  background-color: var(--color-engel-light);
}
.sidebar-box__sub-title {
  margin-top: 0;
  margin-bottom: var(--opehuone--spacing--small);
  font-size: var(--opehuone--font-size--large);
  font-weight: 500;
}
.sidebar-box__sub-title.sidebar-box__sub-title--margin-top {
  margin-top: var(--opehuone--spacing--small);
}

.service-failure {
  position: relative;
  padding: 1rem 1.5rem;
  background-color: var(--color-info-light);
  margin-bottom: var(--opehuone--spacing--normal);
}
.service-failure:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 8px;
  background-color: var(--color-info);
}
.service-failure.service-failure--error {
  background-color: var(--color-error-light);
}
.service-failure.service-failure--error:before {
  background-color: var(--color-error);
}
.service-failure__title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
  margin-top: 0;
  margin-bottom: var(--opehuone--font-size--normal);
  font-weight: 700;
  font-size: var(--opehuone--font-size--medium);
}
.service-failure p {
  font-size: var(--opehuone--font-size--small);
  font-weight: 500;
}
.service-failure__toggler {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  gap: 15px;
  background-color: transparent;
  border: none;
  font-size: var(--opehuone--font-size--small);
  font-weight: 500;
  padding: 0;
}
.service-failure__toggler:hover, .service-failure__toggler:focus {
  text-decoration: underline;
}
.service-failure--read-more-open .service-failure__toggler > svg {
  transform: rotate(180deg);
}
.service-failure__read-more-section {
  display: none;
  margin-top: var(--opehuone--spacing--small);
}
.service-failure--read-more-open .service-failure__read-more-section {
  display: block;
}

.user-favs-list {
  list-style-type: none;
  margin: 0 0 var(--opehuone--spacing--medium) 0;
  padding: 0;
  display: grid;
  grid-template-columns: auto;
  gap: 1.375rem;
}
.user-favs-list.user-favs-list--grid {
  margin: 0;
}
@media (min-width: 576px) {
  .user-favs-list.user-favs-list--grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .user-favs-list.user-favs-list--grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.user-favs-list__item {
  position: relative;
  padding-left: 1.25rem;
}
.user-favs-list__item:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 8px;
  height: 100%;
  background-color: var(--color-tram);
}
.user-favs-list__link {
  display: grid;
  grid-template-columns: auto;
  gap: 0.3125rem;
  text-decoration: none;
}
.user-favs-list__link:hover, .user-favs-list__link:focus {
  text-decoration: underline;
  display: grid;
}
.user-favs-list__link-category {
  font-size: var(--opehuone--font-size--small);
  font-weight: 400;
}
.user-favs-list__link-title {
  font-size: var(--opehuone--font-size--normal);
  font-weight: 500;
}
.user-favs-list__edit-link {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  gap: 0.625rem;
  background: none;
  border: none;
  font-size: var(--opehuone--font-size--medium);
  font-weight: 700;
  text-decoration: none;
  padding: 0;
}
.user-favs-list__edit-link:hover, .user-favs-list__edit-link:focus {
  text-decoration: underline;
}

.front-page-posts-filter__title {
  margin-top: 0;
  margin-bottom: var(--opehuone--spacing--x-small);
  font-weight: 400;
  font-size: var(--opehuone--font-size--x-large);
}
.front-page-posts-filter__checkboxes-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: var(--opehuone--spacing--small);
  margin-bottom: var(--opehuone--spacing--small);
}

.front-page-posts-filter__checkbox-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: var(--opehuone--font-size--small);
  font-weight: 400;
}

.front-page-posts-filter__checkbox-input {
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  margin: 0;
  position: relative;
  font: inherit;
  color: currentColor;
  width: 22px;
  height: 22px;
  border: 2px solid #808080;
  border-radius: 0;
}
.front-page-posts-filter__checkbox-input:checked {
  background-color: #0072c6;
  border: 2px solid #0072c6;
}
.front-page-posts-filter__checkbox-input:checked:before {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-position: center;
  background-size: auto;
  background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' viewBox='0 0 22 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='3' y='3' width='18' height='18' fill='%2300005E' stroke='%2300005E' stroke-width='2'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M18.5 6.83203L9.33333 15.9987L4.75 11.4154L6 10.1654L9.33333 13.4987L17.25 5.58203L18.5 6.83203Z' fill='white'/%3E%3Crect x='1' y='1' width='20' height='20' stroke='%230072C6' stroke-width='2'/%3E%3C/svg%3E%0A");
}

.profile-opener-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 1px solid var(--color-black-20);
}
@media (min-width: 992px) {
  .profile-opener-wrapper {
    position: relative;
  }
}
.profile-opener-wrapper.profile-opener-wrapper--dropdown-open .profile-opener-dropdown {
  display: flex;
}
.profile-opener-wrapper.profile-opener-wrapper--dropdown-open .profile-opener:after {
  transform: rotate(180deg);
}

.profile-opener {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  background: none;
}
.profile-opener > span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  color: #fff;
  background: var(--color-bus);
  border: none;
  font-weight: 500;
  font-size: var(--opehuone--font-size--medium);
}
.profile-opener:after {
  content: "";
  margin-left: 5px;
  width: 14px;
  height: 9px;
  background-image: url("data:image/svg+xml,%3Csvg width='14' height='9' viewBox='0 0 14 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7 5.5L12 0.5L13.5 2L7 8.5L0.5 2L2 0.5L7 5.5Z' fill='black'/%3E%3C/svg%3E%0A");
}

