  display: flex;
  align-items: center;
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='14' viewBox='0 0 9 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.5 7L0.5 2L2 0.5L8.5 7L2 13.5L0.5 12L5.5 7Z' fill='black'/%3E%3C/svg%3E%0A");
  background-position: 100%;
