.icon-detail__subtitle {
  display: block;
  font-size: var(--opehuone--font-size--small);
  line-height: 1.7142857143;
  font-weight: 500;
  margin-bottom: 0;
}

.checkbox-filter__filter-btn {
  position: relative;
  width: 100%;
  height: 3.5rem;
  padding: 0.9375rem 1rem;
  font-size: var(--opehuone--font-size--medium);
  line-height: 1.5;
