  border-radius: 8px;
}

/* Add horizontal margin to nested blocks/elements. */
.wp-block-details.is-style-stylish > :where(:not(summary)) {
  margin-left: 2rem;
  margin-right: 2rem;
}

/* If open, add some bottom padding to avoid content butting against the bottom. */
.wp-block-details.is-style-stylish[open] {
  padding-bottom: 2rem;
}

.wp-block-details.is-style-stylish summary {
  position: relative;
  transition: all 0.5s ease-in-out;
  box-sizing: border-box;
  padding: 2rem;
  font-weight: 500;
}

