  background: var(--wp--preset--color--contrast-3);
  color: var(--wp--preset--color--base);
}

.wp-block-details.is-style-stylish summary::marker {
  content: none;
}

/* Additional Safari-specific CSS to hide the marker */
.wp-block-details.is-style-stylish summary::-webkit-details-marker {
  display: none; /* Safari-specific property */
}

.wp-block-details.is-style-stylish summary::after,
.wp-block-details.is-style-stylish summary::before {
  background-color: var(--wp--preset--color--base);
  content: "";
  height: 1rem;
  position: absolute;
  transition: transform 0.25s ease-out;
  width: 1rem;
  top: 0;
  bottom: 0;
  margin: auto 0;
}

