.b-reg-deadline-training-list {
	display: flex;
	flex-direction: column;
	gap: 0;
	margin: 0;
	padding: 0;
	list-style-type: none;

	&__item {
		position: relative;
		padding: var(--opehuone--spacing--small)
			var(--opehuone--spacing--2-x-small);

		&:after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 1px;
			background-color: #cccccc;
		}
	}

	&__title {
		display: block;
		font-size: var(--opehuone--font-size--medium);
		font-weight: 700;
		line-height: 1.5;
		margin-bottom: var(--opehuone--spacing--2-x-small);
		text-decoration: none;

		@include hover-focus {
			text-decoration: underline;
		}

		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}

	&__categories {
		font-size: var(--opehuone--font-size--small);
		font-weight: 400;
		line-height: calc(20 / 14);
		margin-bottom: var(--opehuone--spacing--2-x-small);
	}

	&__icon-row {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-direction: row;
		gap: rem(12px);
		font-weight: 500;
		line-height: 1;
		font-size: var(--opehuone--font-size--small);

		> svg {
			width: rem(20px);
			height: auto;
			flex-shrink: 0;
		}
	}
}
